<template>
	<div class="Content">
		<!-- <mu-icon value="home" :size="126"/>		 -->
		<h3>
			{{city}}
		</h3>
		<div v-if="loading == true">
		  <mu-circular-progress :size="90" color="red"/>
		</div>

		<!-- 天气动画 -->
		<Weather v-bind:weather_info="weather_info"></Weather>
	</div>
</template>

<script>
	import axios from 'axios';
	import ajax  from '../tools/ajax';
	import Weather from './Weather';
	const url ='http://api.map.baidu.com/location/ip';
	const key ='AK94PiFGKtFmiFz8hWSwEgxFZE4GVkmE';
	const weather_url ='https://bird.ioliu.cn/weather';
	export default{
		data(){
			return {
				loading : false,
				city:"获取中....",
				weather_info:{}
			}
		},
		components : {
			Weather
		},
		created: function(){
			let that = this;
			ajax.jsonp(url,{ak:key},function(res){
				that.city=res.content.address_detail.city
			},this);
		},
		watch:{
			city:function(){
				var that = this;
				ajax.get(weather_url,{
					params:{
						city:this.city
					}
				},function(res){
					that.weather_info = res.data;
				},this);				
			}
		}
	}
</script>

<style scoped>
	.Content{
		width: 80%;
		margin: auto;
		margin-top: 2rem;
		text-align: center;		
	}
	.Content h3{
		font-size: 3.2rem;
	}
</style>


