import axios from 'axios';
import axios_jsonp from 'axios-jsonp-pro';
import qs from 'qs';
export default{
	get(url,data,callback,that){
		that.loading = true;
		axios.get(url,data).then((data,err)=>{
			if(!err){
				callback(data);

			}
			that.loading = false;
		});
	},
	post(url,data,callback,that){
		axios.post(url,qs.stringify(data),{
			headers: {
		      'Access-Control-Allow-Origin': '*',
		      'Content-Type': 'application/json',
		    }
		}).then((data,err)=>{
			if(!err){
				callback(data);

			}
			that.loading = false;
		});
	},
	jsonp(url,data,callback,that){
		that.loading = true;
		axios_jsonp.jsonp(url,{
		    params:data
		}).then((data,err)=>{
			if(!err){
				callback(data);
			}
			that.loading = false;
		});
	}
}


