<template>
	<div class="nav">
	  	<mu-paper style="max-width: 100%; ">
			<mu-bottom-nav :value="bottomNav"  @change="handleChange">
				<mu-bottom-nav-item value="home" title="主页" icon="directions_car"/>
				<mu-bottom-nav-item value="loyalty" title="建议" icon="loyalty"/>
				<!-- <mu-bottom-nav-item value="pictures" title="Pictures" icon="photo"/> -->
			</mu-bottom-nav>
	  	</mu-paper>
	  	<div class="Content">
			<WashCar></WashCar>		
	  	</div>
	</div>
</template>


<script>
	import WashCar from './WashCar';
	export default {
		data () {
		    return {
		      bottomNav: 'home',
		      bottomNavColor: 'home'
		    }
		},
		components : {
			WashCar
		},
	  	methods: {
		    handleChange (val) {
		      this.bottomNav = val
	   		}
  		}
	}
</script>


<style scoped>
	
</style>


